import React, { useState, useEffect } from 'react';
import Pages from './pages';
import LinearProgress from '@material-ui/core/LinearProgress';

function LoadingPage() {

  return (
    <div>
      <LinearProgress/>
    </div>
  );
}

const hobbiesDrawings = [
  'https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/f49c7b90-64db-4ebd-af85-5ac876c901d2/d1xu78k-f8cba24d-80ba-4937-adc6-7596084ccfe5.png/v1/fill/w_600,h_480,q_80,strp/naruto_e_il_quarto_hokage_by_randalf89_d1xu78k-fullview.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..jXS_Z_1c8eE01R-CdH_UkMyCq82cYxBj-8Llyjr4IkE',
  'https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/f49c7b90-64db-4ebd-af85-5ac876c901d2/d1xu9mn-6bfd7807-949f-4c63-85f8-ce05396b7ff4.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..hMklIzLMkUiSR8T7q8n4GZ1LEc8pi3xGaqRSG-9NfmM',
  'https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/f49c7b90-64db-4ebd-af85-5ac876c901d2/d1xu9gt-d0a3a5ae-b39b-4c8c-bcf5-aa7491aef5c2.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..NJ8kiC1Zl5LnCNCPvb1LVTMm78PbJFuVhBWb7nWm8ro',
  'https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/f49c7b90-64db-4ebd-af85-5ac876c901d2/d1xu7fz-f6e7e8db-41e5-44ac-bd11-46ef35e008fe.jpg/v1/fill/w_600,h_420,q_75,strp/randalf_e_ninagirl_by_randalf89_d1xu7fz-fullview.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..byrYkQqdjODqvNRsamAOdMMJsovcux4ANjT-TL2oHwk',
  'https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/f49c7b90-64db-4ebd-af85-5ac876c901d2/d1xu8hz-7da63628-b392-44ce-bebb-7e29a8052073.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..ilBi9NWJ-tFPvA66mowHcgG2tnPsK4LImhSfueMnuaU'
]

const hobbyIntro = 'When I was a child I had always liked to create experiences for other people. One of my first hobby was drawing fan made comics inspired by Japanese manga. I also enjoyed creating cardboard games about those, something not very distant from Pokemon cards or Monopoly, but Dragon Ball style. '

function App() {
  const [user, setUser] = useState(null);

  const hobbies = {
    intro: hobbyIntro,
    drawings: hobbiesDrawings

  }

  useEffect(() => {
    // NOTE: Use your username below
    fetch('https://gitconnected.com/v1/portfolio/darix989')
      .then(res => res.json())
      .then(user => {
        setUser(user);
      });
  }, []);

  if (!user) {
    return <LoadingPage/>;
  }

  return <Pages user={user} hobbies={hobbies} />;
}

export default App;
